import { useState, useRef, Fragment, useEffect } from 'react';
import { Dialog, Transition } from '@headlessui/react';
import { DEFAULT_METRICS, METRIC_BANK } from './constants/metrics';
import { Menu } from '@headlessui/react';
import * as XLSX from 'xlsx';
import { extractMetricWithThrottle, ExtractionError } from './services/extractionService';
import HistorySidebar from './components/HistorySidebar';
import useExtractionHistory from './hooks/useExtractionHistory';
import useMetricTemplates from './hooks/useMetricTemplates';
import { format } from 'date-fns';

function MetricModal({ isOpen, closeModal, metric, onSave, title, currentMetrics, availableMetrics }) {
  const [name, setName] = useState('');
  const [description, setDescription] = useState('');

  useEffect(() => {
    if (metric) {
      setName(metric.name);
      setDescription(metric.description);
    } else {
      setName('');
      setDescription('');
    }
  }, [metric]);

  const handleSubmit = (e) => {
    e.preventDefault();
    onSave({ id: metric?.id || `custom-${Date.now()}`, name, description });
    closeModal();
  };

  return (
    <Transition appear show={isOpen} as={Fragment}>
      <Dialog as="div" className="relative z-10" onClose={closeModal}>
        <Transition.Child
          as={Fragment}
          enter="ease-out duration-300"
          enterFrom="opacity-0"
          enterTo="opacity-100"
          leave="ease-in duration-200"
          leaveFrom="opacity-100"
          leaveTo="opacity-0"
        >
          <div className="fixed inset-0 bg-black bg-opacity-25" />
        </Transition.Child>

        <div className="fixed inset-0 overflow-y-auto">
          <div className="flex min-h-full items-center justify-center p-4">
            <Transition.Child
              as={Fragment}
              enter="ease-out duration-300"
              enterFrom="opacity-0 scale-95"
              enterTo="opacity-100 scale-100"
              leave="ease-in duration-200"
              leaveFrom="opacity-100 scale-100"
              leaveTo="opacity-0 scale-95"
            >
              <Dialog.Panel className="w-full max-w-2xl transform overflow-hidden rounded-2xl bg-finance-900 p-6 text-left align-middle shadow-xl transition-all">
                <Dialog.Title
                  as="h3"
                  className="text-lg font-medium leading-6 text-finance-100 mb-4"
                >
                  {title}
                </Dialog.Title>

                {!metric && availableMetrics.length > 0 && (
                  <div className="mb-8">
                    <h4 className="text-sm font-medium text-finance-200 mb-3">Available Metrics</h4>
                    <div className="grid grid-cols-2 gap-4">
                      {availableMetrics.map(bankMetric => (
                        <button
                          key={bankMetric.id}
                          onClick={() => onSave(bankMetric)}
                          className="text-left p-4 rounded-lg bg-finance-800 hover:bg-finance-700 transition-colors"
                        >
                          <div className="font-medium text-finance-100">{bankMetric.name}</div>
                          <div className="text-sm text-finance-300 mt-1">{bankMetric.description}</div>
                        </button>
                      ))}
                    </div>
                  </div>
                )}

                {!metric && availableMetrics.length > 0 && (
                  <div className="relative my-8">
                    <div className="absolute inset-0 flex items-center">
                      <div className="w-full border-t border-finance-700"></div>
                    </div>
                    <div className="relative flex justify-center">
                      <span className="px-4 bg-finance-900 text-finance-300 text-sm">
                        Or Create Custom Metric
                      </span>
                    </div>
                  </div>
                )}

                <form onSubmit={handleSubmit}>
                  <div className="space-y-4">
                    <div>
                      <label className="block text-sm font-medium text-finance-200">
                        Metric Name
                      </label>
                      <input
                        type="text"
                        value={name}
                        onChange={(e) => setName(e.target.value)}
                        className="mt-1 block w-full rounded-md bg-finance-800 border-finance-700 text-finance-100 shadow-sm 
                                   focus:border-finance-500 focus:ring-finance-500 px-4 py-2"
                        required
                      />
                    </div>
                    <div>
                      <label className="block text-sm font-medium text-finance-200">
                        Description
                      </label>
                      <textarea
                        value={description}
                        onChange={(e) => setDescription(e.target.value)}
                        rows={3}
                        className="mt-1 block w-full rounded-md bg-finance-800 border-finance-700 text-finance-100 shadow-sm 
                                   focus:border-finance-500 focus:ring-finance-500 px-4 py-2"
                        required
                      />
                    </div>
                  </div>
                  <div className="mt-6 flex justify-end space-x-3">
                    <button
                      type="button"
                      onClick={closeModal}
                      className="px-4 py-2 text-sm font-medium text-finance-300 hover:text-finance-200"
                    >
                      Cancel
                    </button>
                    <button
                      type="submit"
                      className="px-4 py-2 text-sm font-medium bg-finance-600 text-white rounded-md hover:bg-finance-700"
                    >
                      Save
                    </button>
                  </div>
                </form>
              </Dialog.Panel>
            </Transition.Child>
          </div>
        </div>
      </Dialog>
    </Transition>
  );
}

function App() {
  const [metrics, setMetrics] = useState(DEFAULT_METRICS);
  const [documents, setDocuments] = useState([]);
  const [extractionStatus, setExtractionStatus] = useState({});
  const [availableBankMetrics, setAvailableBankMetrics] = useState(
    METRIC_BANK.filter(bankMetric => !DEFAULT_METRICS.some(m => m.id === bankMetric.id))
  );
  const fileInputRef = useRef(null);
  const [modalMetric, setModalMetric] = useState(null);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [modalType, setModalType] = useState('');

  const {
    templates,
    saveTemplate,
    deleteTemplate,
    updateTemplate
  } = useMetricTemplates();

  const {
    histories,
    selectedHistoryId,
    setSelectedHistoryId,
    saveCurrentExtraction,
    getHistoryById
  } = useExtractionHistory();

  const isHistoryView = selectedHistoryId !== null;

  const handleFileSelect = (event) => {
    const files = Array.from(event.target.files);
    const newDocs = files.map(file => ({
      id: Date.now() + Math.random(),
      file: file,
      name: file.name
    }));
    setDocuments([...documents, ...newDocs]);
    event.target.value = '';
  };

  const removeDocument = (id) => {
    setDocuments(documents.filter(doc => doc.id !== id));
  };

  const handleMetricSave = (updatedMetric) => {
    if (modalType === 'edit') {
      setMetrics(metrics.map(m => m.id === updatedMetric.id ? updatedMetric : m));
    } else {
      const isFromBank = availableBankMetrics.some(m => m.id === updatedMetric.id);
      if (isFromBank) {
        setAvailableBankMetrics(availableBankMetrics.filter(m => m.id !== updatedMetric.id));
      } else {
        updatedMetric = { ...updatedMetric, id: `custom-${Date.now()}` };
      }
      setMetrics([...metrics, updatedMetric]);
    }
  };

  const removeMetric = (id) => {
    const bankMetric = METRIC_BANK.find(m => m.id === id);
    if (bankMetric) {
      setAvailableBankMetrics(prev => [...prev, bankMetric]);
    }
    setMetrics(metrics.filter(metric => metric.id !== id));
  };

  const openModal = (type, metric = null) => {
    setModalType(type);
    setModalMetric(metric);
    setIsModalOpen(true);
  };

  const handleExport = (format) => {
    // Prepare the data based on format
    const getExportData = (includeDescription = false) => {
      return documents.map(doc => {
        const row = {
          Document: doc.name,
        };
        
        metrics.forEach(metric => {
          // For XLSX and CSV, include description in header
          const columnHeader = includeDescription 
            ? `${metric.name}:\n${metric.description}`
            : metric.name;
          row[columnHeader] = extractionStatus[`${doc.id}-${metric.id}`]?.result?.value || '';
        });
        
        return row;
      });
    };

    switch (format) {
      case 'xlsx':
        // Use formatted headers for XLSX
        const xlsxData = getExportData(true);
        const ws = XLSX.utils.json_to_sheet(xlsxData);
        
        // Adjust column widths to fit the content
        const columnWidths = Object.keys(xlsxData[0]).map(key => ({
          wch: Math.max(20, key.length)
        }));
        ws['!cols'] = columnWidths;
        
        const wb = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(wb, ws, 'Extraction Results');
        XLSX.writeFile(wb, 'financial_metrics_extraction.xlsx');
        break;

      case 'csv':
        // Use formatted headers for CSV
        const csvData = getExportData(true);
        const csvWs = XLSX.utils.json_to_sheet(csvData);
        const csv = XLSX.utils.sheet_to_csv(csvWs);
        
        const csvBlob = new Blob([csv], { type: 'text/csv;charset=utf-8;' });
        const csvLink = document.createElement('a');
        if (navigator.msSaveBlob) {
          navigator.msSaveBlob(csvBlob, 'financial_metrics_extraction.csv');
        } else {
          csvLink.href = URL.createObjectURL(csvBlob);
          csvLink.download = 'financial_metrics_extraction.csv';
          csvLink.style.display = 'none';
          document.body.appendChild(csvLink);
          csvLink.click();
          document.body.removeChild(csvLink);
        }
        break;

      case 'json':
        // Use simple format for JSON
        const jsonData = getExportData(false);
        const jsonContent = JSON.stringify(jsonData, null, 2);
        const jsonBlob = new Blob([jsonContent], { type: 'application/json' });
        const jsonLink = document.createElement('a');
        if (navigator.msSaveBlob) {
          navigator.msSaveBlob(jsonBlob, 'financial_metrics_extraction.json');
        } else {
          jsonLink.href = URL.createObjectURL(jsonBlob);
          jsonLink.download = 'financial_metrics_extraction.json';
          jsonLink.style.display = 'none';
          document.body.appendChild(jsonLink);
          jsonLink.click();
          document.body.removeChild(jsonLink);
        }
        break;

      default:
        console.error('Unsupported export format');
    }
  };

  const extractCell = async (docId, metricId) => {
    const doc = documents.find(d => d.id === docId);
    const metric = metrics.find(m => m.id === metricId);
    const cellKey = `${docId}-${metricId}`;

    try {
      setExtractionStatus(prev => ({
        ...prev,
        [cellKey]: { status: 'loading' }
      }));

      const result = await extractMetricWithThrottle({
        file: doc.file,
        metricName: metric.name,
        metricDescription: metric.description
      });

      setExtractionStatus(prev => ({
        ...prev,
        [cellKey]: { 
          status: 'complete',
          result: result.value,
          confidence: result.confidence,
          metadata: result.metadata
        }
      }));
    } catch (error) {
      setExtractionStatus(prev => ({
        ...prev,
        [cellKey]: { 
          status: 'error',
          error: error instanceof ExtractionError ? error.message : 'Extraction failed'
        }
      }));
    }
  };

  const handleStartExtraction = async () => {
    // Create extraction requests for all cells
    const extractionPromises = documents.map(doc => 
      metrics.map(metric => {
        const cellKey = `${doc.id}-${metric.id}`;
        
        // Set loading state for the cell
        setExtractionStatus(prev => ({
          ...prev,
          [cellKey]: { status: 'loading' }
        }));

        // Return the extraction promise
        return extractMetricWithThrottle({
          file: doc.file,
          metricName: metric.name,
          metricDescription: metric.description
        }).then(result => {
          // Update cell status with result
          setExtractionStatus(prev => ({
            ...prev,
            [cellKey]: { 
              status: 'complete',
              result: result
            }
          }));
          return { cellKey, result };
        });
      })
    ).flat();

    // Wait for all extractions to complete and collect results
    const results = await Promise.all(extractionPromises);
    
    // Create final extraction status object
    const finalExtractionStatus = results.reduce((acc, { cellKey, result }) => ({
      ...acc,
      [cellKey]: {
        status: 'complete',
        result: result
      }
    }), {});

    // Save to history without switching to history view
    saveCurrentExtraction(documents, metrics, finalExtractionStatus);
  };

  const renderCell = (docId, metricId) => {
    const status = extractionStatus[`${docId}-${metricId}`];
    
    if (!status) return '-';
    
    switch (status.status) {
      case 'loading':
        return <div className="animate-pulse bg-finance-800 h-4 w-16 rounded"></div>;
      case 'complete':
        if (status.result?.value === 'Exception') {
          return (
            <span className="text-red-400" title={status.result.error}>
              Error
            </span>
          );
        }
        return <span>{status.result?.value || status.result}</span>;
      default:
        return '-';
    }
  };

  const handleSelectHistory = (history) => {
    setSelectedHistoryId(history.id);
    setMetrics(history.metrics);
    setDocuments(history.documents);
    
    // Convert the extraction status back to the correct format
    const restoredStatus = Object.entries(history.extractionStatus).reduce((acc, [key, value]) => ({
      ...acc,
      [key]: {
        status: 'complete',
        result: value.result
      }
    }), {});
    
    setExtractionStatus(restoredStatus);
  };

  const handleNewExtraction = () => {
    setSelectedHistoryId(null);
    setMetrics(DEFAULT_METRICS);
    setDocuments([]);
    setExtractionStatus({});
  };

  return (
    <div className="flex min-h-screen bg-gradient-to-br from-finance-950 via-finance-900 to-finance-800">
      <HistorySidebar
        histories={histories}
        onSelectHistory={handleSelectHistory}
        onNewExtraction={handleNewExtraction}
        selectedHistoryId={selectedHistoryId}
        templates={templates}
        onSelectTemplate={(template) => setMetrics(template.metrics)}
        onSaveTemplate={(name) => saveTemplate(name, metrics)}
        onDeleteTemplate={deleteTemplate}
        metrics={metrics}
      />

      <div className="flex-1 ml-64">
        <div className="max-w-7xl mx-auto p-8">
          <div className="flex justify-between items-center mb-8">
            <h1 className="text-3xl font-bold text-white">
              {isHistoryView 
                ? `Extraction Results - ${format(new Date(getHistoryById(selectedHistoryId).timestamp), 'yyyy-MM-dd HH:mm:ss')}`
                : 'Financial Metric Extraction'
              }
            </h1>
          </div>

          <div className="bg-white bg-opacity-10 backdrop-blur-lg rounded-xl shadow-2xl">
            <div className="overflow-x-auto">
              <table className="min-w-full divide-y divide-gray-700">
                <thead>
                  <tr className="bg-finance-900 bg-opacity-50">
                    <th className="px-6 py-4 text-left text-xs font-semibold text-finance-100 uppercase tracking-wider w-72">
                      Document
                    </th>
                    {metrics.map(metric => (
                      <th
                        key={metric.id}
                        className="px-6 py-4 text-left text-xs font-semibold text-finance-100 uppercase tracking-wider group relative"
                        title={metric.description}
                      >
                        <div className="flex items-center">
                          <span>{metric.name}</span>
                          {!isHistoryView && (
                            <div className="hidden group-hover:flex items-center gap-2 ml-2 absolute right-2">
                              <button
                                onClick={() => openModal('edit', metric)}
                                className="text-finance-300 hover:text-finance-200 p-1 rounded bg-finance-800 bg-opacity-75 hover:bg-finance-700"
                              >
                                <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4" viewBox="0 0 20 20" fill="currentColor">
                                  <path d="M13.586 3.586a2 2 0 112.828 2.828l-.793.793-2.828-2.828.793-.793zM11.379 5.793L3 14.172V17h2.828l8.38-8.379-2.83-2.828z" />
                                </svg>
                              </button>
                              <button
                                onClick={() => removeMetric(metric.id)}
                                className="text-red-400 hover:text-red-300 p-1 rounded bg-finance-800 bg-opacity-75 hover:bg-finance-700"
                              >
                                <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4" viewBox="0 0 20 20" fill="currentColor">
                                  <path fillRule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clipRule="evenodd" />
                                </svg>
                              </button>
                            </div>
                          )}
                        </div>
                      </th>
                    ))}
                    {!isHistoryView && (
                      <th className="px-6 py-4 w-20">
                        <button
                          onClick={() => openModal('add')}
                          className="text-finance-300 hover:text-finance-200"
                          title="Add new metric"
                        >
                          <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                            <path fillRule="evenodd" d="M10 3a1 1 0 011 1v5h5a1 1 0 110 2h-5v5a1 1 0 11-2 0v-5H4a1 1 0 110-2h5V4a1 1 0 011-1z" clipRule="evenodd" />
                          </svg>
                        </button>
                      </th>
                    )}
                  </tr>
                </thead>
                <tbody className="divide-y divide-gray-700">
                  {documents.map(doc => (
                    <tr key={doc.id} className="group hover:bg-finance-900 hover:bg-opacity-30 transition-colors">
                      <td className="px-6 py-4 relative">
                        <div className="flex items-center">
                          <div className="flex items-center flex-1 min-w-0">
                            <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5 text-finance-300 mr-2 flex-shrink-0" viewBox="0 0 20 20" fill="currentColor">
                              <path fillRule="evenodd" d="M4 4a2 2 0 012-2h4.586A2 2 0 0112 2.586L15.414 6A2 2 0 0116 7.414V16a2 2 0 01-2 2H6a2 2 0 01-2-2V4zm2 6a1 1 0 011-1h6a1 1 0 110 2H7a1 1 0 01-1-1zm1 3a1 1 0 100 2h6a1 1 0 100-2H7z" clipRule="evenodd" />
                            </svg>
                            <span className="text-finance-100 truncate" title={doc.name}>
                              {doc.name}
                            </span>
                          </div>
                        </div>
                        {!isHistoryView && (
                          <div className="hidden group-hover:block absolute right-6 top-1/2 -translate-y-1/2">
                            <button
                              onClick={() => removeDocument(doc.id)}
                              className="text-red-400 hover:text-red-300 p-1 rounded bg-finance-800 bg-opacity-75 hover:bg-finance-700"
                            >
                              <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4" viewBox="0 0 20 20" fill="currentColor">
                                <path fillRule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clipRule="evenodd" />
                              </svg>
                            </button>
                          </div>
                        )}
                      </td>
                      {metrics.map(metric => (
                        <td key={metric.id} className="px-6 py-4 text-finance-100">
                          {renderCell(doc.id, metric.id)}
                        </td>
                      ))}
                      <td></td>
                    </tr>
                  ))}
                  {documents.length === 0 && (
                    <tr>
                      <td colSpan={metrics.length + 2} className="px-6 py-8 text-center text-finance-100">
                        <p className="text-lg font-medium">No documents added yet</p>
                        <p className="text-sm text-finance-300 mt-1">Click the "Add Documents" button to get started</p>
                      </td>
                    </tr>
                  )}
                </tbody>
              </table>
            </div>

            <input
              type="file"
              ref={fileInputRef}
              className="hidden"
              accept=".pdf"
              multiple
              onChange={handleFileSelect}
            />

            <div className="p-4 border-t border-gray-700">
              <div className="mb-4 flex justify-between items-center">
                <div className="flex gap-4">
                  {!isHistoryView && (
                    <>
                      <button
                        onClick={() => fileInputRef.current.click()}
                        className="px-6 py-2.5 bg-gradient-to-r from-finance-500 to-finance-600 
                                  text-white font-medium rounded-lg shadow-lg hover:from-finance-600 
                                  hover:to-finance-700 transition-all duration-200 flex items-center gap-2"
                      >
                        <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                          <path fillRule="evenodd" d="M10 3a1 1 0 011 1v5h5a1 1 0 110 2h-5v5a1 1 0 11-2 0v-5H4a1 1 0 110-2h5V4a1 1 0 011-1z" clipRule="evenodd" />
                        </svg>
                        Add Documents
                      </button>

                      <button
                        onClick={handleStartExtraction}
                        disabled={documents.length === 0}
                        className={`px-6 py-2.5 font-medium rounded-lg shadow-lg flex items-center gap-2
                                  ${documents.length === 0 
                                    ? 'bg-gray-600 text-gray-300 cursor-not-allowed' 
                                    : 'bg-gradient-to-r from-green-600 to-green-700 text-white hover:from-green-700 hover:to-green-800'} 
                                  transition-all duration-200`}
                      >
                        <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                          <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM9.555 7.168A1 1 0 008 8v4a1 1 0 001.555.832l3-2a1 1 0 000-1.664l-3-2z" clipRule="evenodd" />
                        </svg>
                        Start Extraction
                      </button>
                    </>
                  )}
                </div>

                <Menu as="div" className="relative">
                  <Menu.Button
                    disabled={documents.length === 0}
                    className={`px-6 py-2.5 font-medium rounded-lg shadow-lg flex items-center gap-2
                              ${documents.length === 0 
                                ? 'bg-gray-600 text-gray-300 cursor-not-allowed' 
                                : 'bg-gradient-to-r from-finance-600 to-finance-700 text-white hover:from-finance-700 hover:to-finance-800'} 
                              transition-all duration-200`}
                  >
                    <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                      <path fillRule="evenodd" d="M3 17a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zm3.293-7.707a1 1 0 011.414 0L9 10.586V3a1 1 0 112 0v7.586l1.293-1.293a1 1 0 111.414 1.414l-3 3a1 1 0 01-1.414 0l-3-3a1 1 0 010-1.414z" clipRule="evenodd" />
                    </svg>
                    Export
                  </Menu.Button>

                  <Transition
                    as={Fragment}
                    enter="transition ease-out duration-100"
                    enterFrom="transform opacity-0 scale-95"
                    enterTo="transform opacity-100 scale-100"
                    leave="transition ease-in duration-75"
                    leaveFrom="transform opacity-100 scale-100"
                    leaveTo="transform opacity-0 scale-95"
                  >
                    <Menu.Items className="absolute right-0 mt-2 w-48 origin-top-right rounded-md bg-finance-800 shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none">
                      <div className="py-1">
                        <Menu.Item>
                          {({ active }) => (
                            <button
                              onClick={() => handleExport('xlsx')}
                              className={`${
                                active ? 'bg-finance-700' : ''
                              } text-finance-100 group flex w-full items-center px-4 py-2 text-sm`}
                            >
                              Export as XLSX
                            </button>
                          )}
                        </Menu.Item>
                        <Menu.Item>
                          {({ active }) => (
                            <button
                              onClick={() => handleExport('csv')}
                              className={`${
                                active ? 'bg-finance-700' : ''
                              } text-finance-100 group flex w-full items-center px-4 py-2 text-sm`}
                            >
                              Export as CSV
                            </button>
                          )}
                        </Menu.Item>
                        <Menu.Item>
                          {({ active }) => (
                            <button
                              onClick={() => handleExport('json')}
                              className={`${
                                active ? 'bg-finance-700' : ''
                              } text-finance-100 group flex w-full items-center px-4 py-2 text-sm`}
                            >
                              Export as JSON
                            </button>
                          )}
                        </Menu.Item>
                      </div>
                    </Menu.Items>
                  </Transition>
                </Menu>
              </div>
            </div>
          </div>
        </div>
      </div>

      <MetricModal
        isOpen={isModalOpen}
        closeModal={() => setIsModalOpen(false)}
        metric={modalMetric}
        onSave={handleMetricSave}
        title={modalType === 'edit' ? 'Edit Metric' : 'Add New Metric'}
        currentMetrics={metrics}
        availableMetrics={availableBankMetrics}
      />
    </div>
  );
}

export default App;
