import { useState, useEffect } from 'react';

const STORAGE_KEY = 'extraction_history';

export default function useExtractionHistory() {
  const [histories, setHistories] = useState(() => {
    // Initialize from localStorage during first render
    try {
      const stored = localStorage.getItem(STORAGE_KEY);
      return stored ? JSON.parse(stored) : [];
    } catch (error) {
      console.error('Failed to load history:', error);
      return [];
    }
  });
  
  const [selectedHistoryId, setSelectedHistoryId] = useState(null);

  // Save to localStorage whenever histories change
  useEffect(() => {
    try {
      const historiesForStorage = histories.map(history => ({
        id: history.id,
        timestamp: history.timestamp,
        documents: history.documents,
        metrics: history.metrics,
        extractionStatus: Object.fromEntries(
          Object.entries(history.extractionStatus).map(([key, value]) => [
            key,
            {
              status: value.status,
              result: value.result,
              error: value.error
            }
          ])
        )
      }));
      
      localStorage.setItem(STORAGE_KEY, JSON.stringify(historiesForStorage));
    } catch (error) {
      console.error('Failed to save history:', error);
    }
  }, [histories]);

  const saveCurrentExtraction = (documents, metrics, extractionStatus) => {
    const newHistory = {
      id: Date.now().toString(),
      timestamp: new Date().toISOString(),
      documents: documents.map(doc => ({
        id: doc.id,
        name: doc.name
      })),
      metrics,
      extractionStatus: Object.fromEntries(
        Object.entries(extractionStatus).map(([key, value]) => [
          key,
          {
            status: value.status,
            result: value.result,
            error: value.error
          }
        ])
      )
    };

    setHistories(prev => [newHistory, ...prev]);
    return newHistory.id;
  };

  const getHistoryById = (id) => {
    return histories.find(h => h.id === id);
  };

  const clearHistory = () => {
    setHistories([]);
    setSelectedHistoryId(null);
    localStorage.removeItem(STORAGE_KEY);
  };

  return {
    histories,
    selectedHistoryId,
    setSelectedHistoryId,
    saveCurrentExtraction,
    getHistoryById,
    clearHistory
  };
} 