import axios from 'axios';

const CHATBOT_APP = 'https://rag.epsilla.com';
const TIMEOUT = 60000; // 60 seconds timeout

// Add configuration constants
const CONFIG = {
  projectId: 'd24e149c-b277-4704-859a-ea8c69c002bf-341931477', // Replace with actual project ID
  appId: 'a3c0ff9a-009f-4a66-bee7-3ff097059a24',         // Replace with actual app ID
  conversationId: 'extraction',   // Can be any string identifier
  apiKey: 'eps_oVF37ZeVZFMSSKaToDhMdWlGOA9hUsTAT5P8f3muEDXxJgPV'  // Add your API key here
};

export class ExtractionError extends Error {
  constructor(message, details = null) {
    super(message);
    this.name = 'ExtractionError';
    this.details = details;
  }
}

export const extractMetric = async ({ file, metricName, metricDescription }) => {
  try {
    // Create the payload for the specific metric
    const payload = {
      message: `You are extracting the financial metric "${metricName}" from the provided document pages.\nThe metric definition is:\n ${metricDescription}`,
    };

    // Create FormData and append file and payload
    const formData = new FormData();
    formData.append('file', file);
    formData.append('json_payload', JSON.stringify(payload));

    // Initial API call to start extraction
    const response = await axios.post(
      `${CHATBOT_APP}/chat-with-one-file/${CONFIG.projectId}/${CONFIG.appId}/${CONFIG.conversationId}`,
      formData,
      {
        headers: {
          'Content-Type': 'multipart/form-data',
          'X-API-Key': CONFIG.apiKey
        },
        timeout: TIMEOUT,
      }
    );

    const messageId = response.data.result;
    let count = 0;
    const maxRetries = Math.floor(TIMEOUT / 150);

    // Poll for results
    for (let i = 0; i < maxRetries; i++) {
      const streamResponse = await axios.get(
        `${CHATBOT_APP}/stream/${CONFIG.projectId}/${CONFIG.appId}/${messageId}`,
        {
          headers: {
            'X-API-Key': CONFIG.apiKey
          }
        }
      );
      
      const result = streamResponse.data.result;

      if (result.completed) {
        const extractedValue = result.result['Generated Result'];
        
        // Handle error cases
        if (extractedValue.endsWith('Error generating the result ...')) {
          throw new ExtractionError('Extraction failed', result);
        }
        
        if (extractedValue === 'Message usage excced the quota in current plan') {
          throw new ExtractionError('Quota exceeded');
        }

        try {
          // Parse the response and extract the value
          const parsedResponse = JSON.parse(extractedValue);
          // Get the first value from the response object
          const metricValue = Object.values(parsedResponse)[0];

          return {
            value: metricValue || 'N/A', // Use the extracted value or N/A if not found
            confidence: result.result.confidence || null,
            metadata: {
              messageId,
              progress: result.result.progress || [],
              knowledge: result.result.knowledge || []
            }
          };
        } catch (error) {
          // If parsing fails, return the raw value
          return {
            value: extractedValue,
            confidence: result.result.confidence || null,
            metadata: {
              messageId,
              progress: result.result.progress || [],
              knowledge: result.result.knowledge || []
            }
          };
        }
      }

      // Wait before next poll
      await new Promise(resolve => setTimeout(resolve, 150));
    }
    throw new ExtractionError(
      'Extraction timeout'
    );
  } catch (error) {
    if (error instanceof ExtractionError) {
      throw error;
    }
    throw new ExtractionError(
      'Extraction failed',
      error.response?.data || error.message
    );
  }
};

const extractMetricWithRetry = async (params, retryCount = 0) => {
  const maxRetries = 2;
  
  try {
    return await extractMetric(params);
  } catch (error) {
    if (retryCount < maxRetries - 1) {
      console.log(`Retrying extraction (attempt ${retryCount + 2} of ${maxRetries})...`);
      // Add exponential backoff delay
      await new Promise(resolve => setTimeout(resolve, 1000 * Math.pow(2, retryCount)));
      return extractMetricWithRetry(params, retryCount + 1);
    }
    
    // After all retries are exhausted, return a formatted error result
    return {
      value: 'Exception',
      error: error instanceof ExtractionError ? error.message : 'Extraction failed',
      metadata: {
        error: error.details || error.message,
        retries: retryCount + 1
      }
    };
  }
};

// Update the throttle implementation to use the retry wrapper
export const extractMetricWithThrottle = (() => {
  let runningTasks = 0;
  const maxParallel = 3;
  const queue = [];

  const processQueue = () => {
    while (queue.length > 0 && runningTasks < maxParallel) {
      const { params, resolve } = queue.shift();
      runningTasks++;

      extractMetricWithRetry(params)
        .then(result => {
          runningTasks--;
          resolve(result);
          processQueue();
        });
    }
  };

  return async (params) => {
    return new Promise((resolve) => {
      queue.push({ params, resolve });
      processQueue();
    });
  };
})(); 