import { format } from 'date-fns';
import { useState } from 'react';
import { Dialog, Transition } from '@headlessui/react';
import { Fragment } from 'react';

export default function TemplatesSidebar({ templates, onSelectTemplate, onSaveTemplate, onDeleteTemplate, metrics }) {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [templateName, setTemplateName] = useState('');

  const handleSaveCurrentMetrics = () => {
    setIsModalOpen(true);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (templateName.trim()) {
      onSaveTemplate(templateName.trim(), metrics);
      setTemplateName('');
      setIsModalOpen(false);
    }
  };

  const closeModal = () => {
    setIsModalOpen(false);
    setTemplateName('');
  };

  return (
    <div className="border-t border-finance-700 pt-6 mt-6">
      <div className="flex justify-between items-center mb-4">
        <h2 className="text-sm font-semibold text-finance-300 uppercase tracking-wider">
          Saved Templates
        </h2>
        <button
          onClick={handleSaveCurrentMetrics}
          className="text-finance-300 hover:text-finance-200 p-1 rounded hover:bg-finance-800"
          title="Save current metrics as template"
        >
          <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
            <path d="M7.707 10.293a1 1 0 10-1.414 1.414l3 3a1 1 0 001.414 0l3-3a1 1 0 00-1.414-1.414L11 11.586V6h1a2 2 0 012 2v7a2 2 0 01-2 2H7a2 2 0 01-2-2V8a2 2 0 012-2h1v5.586l-1.293-1.293zM9 4a1 1 0 012 0v2H9V4z" />
          </svg>
        </button>
      </div>

      <div className="space-y-2">
        {templates.map(template => (
          <div
            key={template.id}
            className="group relative bg-finance-800 rounded-lg p-3 hover:bg-finance-700 transition-colors"
          >
            <div className="flex justify-between items-start">
              <div className="flex-1 min-w-0">
                <button
                  onClick={() => onSelectTemplate(template)}
                  className="block text-left w-full"
                >
                  <div className="text-sm font-medium text-finance-100 truncate">
                    {template.name}
                  </div>
                  <div className="text-xs text-finance-400 mt-1">
                    {template.metrics.length} metrics  {format(new Date(template.timestamp), 'MMM d, yyyy')}
                  </div>
                </button>
              </div>
              <button
                onClick={() => onDeleteTemplate(template.id)}
                className="hidden group-hover:block text-finance-400 hover:text-finance-300 ml-2"
                title="Delete template"
              >
                <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4" viewBox="0 0 20 20" fill="currentColor">
                  <path fillRule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clipRule="evenodd" />
                </svg>
              </button>
            </div>
          </div>
        ))}
        {templates.length === 0 && (
          <div className="text-center py-4 text-finance-400 text-sm">
            No saved templates
          </div>
        )}
      </div>
      <Transition appear show={isModalOpen} as={Fragment}>
        <Dialog as="div" className="relative z-10" onClose={closeModal}>
          <Transition.Child
            as={Fragment}
            enter="ease-out duration-300"
            enterFrom="opacity-0"
            enterTo="opacity-100"
            leave="ease-in duration-200"
            leaveFrom="opacity-100"
            leaveTo="opacity-0"
          >
            <div className="fixed inset-0 bg-black bg-opacity-25" />
          </Transition.Child>

          <div className="fixed inset-0 overflow-y-auto">
            <div className="flex min-h-full items-center justify-center p-4">
              <Transition.Child
                as={Fragment}
                enter="ease-out duration-300"
                enterFrom="opacity-0 scale-95"
                enterTo="opacity-100 scale-100"
                leave="ease-in duration-200"
                leaveFrom="opacity-100 scale-100"
                leaveTo="opacity-0 scale-95"
              >
                <Dialog.Panel className="w-full max-w-md transform overflow-hidden rounded-2xl bg-finance-900 p-6 text-left align-middle shadow-xl transition-all">
                  <Dialog.Title
                    as="h3"
                    className="text-lg font-medium leading-6 text-finance-100 mb-4"
                  >
                    Save Template
                  </Dialog.Title>

                  <form onSubmit={handleSubmit}>
                    <div>
                      <label className="block text-sm font-medium text-finance-200">
                        Template Name
                      </label>
                      <input
                        type="text"
                        value={templateName}
                        onChange={(e) => setTemplateName(e.target.value)}
                        className="mt-1 block w-full rounded-md bg-finance-800 border-finance-700 text-finance-100 shadow-sm focus:border-finance-500 focus:ring-finance-500 px-4 py-2"
                        required
                      />
                    </div>
                    <div className="mt-6 flex justify-end space-x-3">
                      <button
                        type="button"
                        onClick={closeModal}
                        className="px-4 py-2 text-sm font-medium text-finance-300 hover:text-finance-200"
                      >
                        Cancel
                      </button>
                      <button
                        type="submit"
                        className="px-4 py-2 text-sm font-medium bg-finance-600 text-white rounded-md hover:bg-finance-700"
                      >
                        Save
                      </button>
                    </div>
                  </form>
                </Dialog.Panel>
              </Transition.Child>
            </div>
          </div>
        </Dialog>
      </Transition>
    </div>
  );
}