import { format } from 'date-fns';
import TemplatesSidebar from './TemplatesSidebar';

export default function HistorySidebar({ histories, onSelectHistory, onNewExtraction, selectedHistoryId, templates, onSelectTemplate, onSaveTemplate, onDeleteTemplate, metrics }) {
  return (
    <div className="w-64 bg-finance-900 border-r border-finance-700 p-4 flex flex-col h-screen fixed left-0 top-0">
      <button
        onClick={onNewExtraction}
        className="w-full px-4 py-2 bg-gradient-to-r from-finance-500 to-finance-600 
                  text-white font-medium rounded-lg shadow-lg hover:from-finance-600 
                  hover:to-finance-700 transition-all duration-200 mb-6"
      >
        New Extraction
      </button>

      <TemplatesSidebar
        templates={templates}
        onSelectTemplate={onSelectTemplate}
        onSaveTemplate={onSaveTemplate}
        onDeleteTemplate={onDeleteTemplate}
        metrics={metrics}
      />

      <h2 className="text-sm font-semibold text-finance-300 uppercase tracking-wider mb-4 mt-6">
        History
      </h2>

      <div className="flex-1 overflow-y-auto space-y-2">
        {histories.map(history => (
          <button
            key={history.id}
            onClick={() => onSelectHistory(history)}
            className={`w-full text-left px-3 py-2 rounded-md transition-colors
                      ${selectedHistoryId === history.id 
                        ? 'bg-finance-700 text-white' 
                        : 'text-finance-300 hover:bg-finance-800'}`}
          >
            <div className="text-sm font-medium">
              {format(new Date(history.timestamp), 'yyyy-MM-dd HH:mm:ss')}
            </div>
            <div className="text-xs mt-1 text-finance-400">
              {history.documents.length} documents, {history.metrics.length} metrics
            </div>
          </button>
        ))}
      </div>
    </div>
  );
}